  max-width: 830px;
  text-align: center;
  font-size: 20px;
  line-height: 32px;
  color: #72787D;
  margin-bottom: 28px;
}
.screen3__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.screen3__info {
