}
.screen3__text {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 24px;
  color: #72787D;
}
.screen3__image {
  max-width: 420px;
  max-height: 280px;
}

.screen3-wrp {
  padding-top: 78px;
  padding-bottom: 143px;
}

.screen3__info_right {
  order: 1;
}

.screen4 {
