}
.screen4__title {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-bottom: 21px;
  font-size: 32px;
  line-height: 48px;
  color: #212B31;
}
.screen4__descr {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-bottom: 55px;
  font-size: 20px;
  line-height: 32px;
  color: #72787D;
}
.screen4__list {
  list-style: none;
