}

.solid-block__wrp {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background-color: #F6F8F9;
  border-radius: 4px;
}
.solid-block__img {
  width: 36px;
  height: 36px;
  margin-right: 26px;
}
.solid-block__text {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 32px;
  color: #212B31;
  margin-right: 66px;
}
.solid-block__text span {
  font-weight: bold;
}
.solid-block__link {
  font-size: 18px;
  line-height: 24px;
  color: #212B31;
  padding: 13px 25px;
  background-color: #fff;
  border: 1px solid #DADEDF;
  border-radius: 4px;
}